@mixin advertisers {
  @include section-fitscreen;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  .articles {
    @include advertisers__articles;
  }

  &__mockups {
    @include advertisers__mockups;
  }

  &__slider {
    @include advertisers__slider;
  }

  @include advertisers--portrait;
  @include advertisers--mobile;
  @include advertisers--landscape;
  @include advertisers--desktop;
}
