@mixin publishers--portrait {
  @include portrait {
    flex-wrap: wrap;
    align-items: flex-start;
    overflow-x: hidden;

    .articles {
      @include section-padding;
    }

    .publishers__mockups {
      @include section-padding;
      flex-basis: 100%;
      min-height: 50vh;
      margin: $lem * 2 0;
    }
  }
}
