@mixin advertisers--portrait {
  @include portrait {
    flex-wrap: wrap;
    align-items: flex-start;
    overflow-x: hidden;

    .articles {
      @include section-padding;
    }

    .advertisers__mockups {
      @include section-padding;
      transform: translate($lem, $lem);
      
      flex-basis: 100%;
      min-height: 50vh;
      margin: $lem * 4 $lem * 2 0;
    }
  }
}
