@mixin everything {
  @include section-fitscreen;

  padding-top: 0;

  // for the mockups below this section
  padding-bottom: 30vh;
  @include desktop {
    padding-bottom: 20vh;
  }


  @include colors(white-on-green);

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__hero {
    @include everything__hero;
  }

  .articles {
    @include everything__articles;
  }

  @include everything--tablet;
}
